import React, { Component } from "react";
import TextInputGroup from "../layout/TextInputGroup";
import DatePicker from "react-datepicker";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { addEvent } from "../../actions/eventActions";

class AddEvent extends Component {
  state = {
    title: "",
    body: "",
    date: "",
    time: "",
    mode: "Online",
    location: "Online Streaming",
    ticket_price: "",
    url:
      "https://github.com/SwathyAmmu/eventsapp/blob/master/images/images.jpg?raw=true",
    errors: {},
  };
  // On file select (from the pop up)
  onFileChange = (event) => {
    // Update the state
    this.setState({ url: event.target.files[0] });
  };
  Changedate = (e) => {
    this.setState({
      date: e,
    });
  };

  onSubmit = (e) => {
    e.preventDefault();

    const {
      id,
      title,
      body,
      date,
      time,
      mode,
      location,
      ticket_price,
      url,
    } = this.state;

    // Check For Errors
    if (title === "") {
      this.setState({ errors: { title: "Title is required" } });
      return;
    }
    if (body === "") {
      this.setState({ errors: { title: "Description is required" } });
      return;
    }
    if (date === "") {
      this.setState({ errors: { title: "Date is required" } });
      return;
    }
    if (ticket_price === "") {
      this.setState({ errors: { title: "Rate is required" } });
      return;
    }

    const newEvent = {
      id,
      title,
      body,
      date,
      time,
      mode,
      location,
      ticket_price,
      url,
    };

    //// SUBMIT Event ////
    this.props.addEvent(newEvent);

    // Clear State
    this.setState({
      title: "",
      body: "",
      date: "",
      time: "",
      mode: "Online",
      location: "Online Streaming",
      ticket_price: "",
      url:
        "https://github.com/SwathyAmmu/eventsapp/blob/master/images/images.jpg?raw=true",
      errors: {},
    });

    this.props.history.push("/events");
  };

  onChange = (e) => this.setState({ [e.target.name]: e.target.value });

  render() {
    const {
      title,
      body,
      date,
      time,
      mode,
      location,
      ticket_price,
      url,
      errors,
    } = this.state;

    return (
      <div className="card mb-3">
        <div className="card-header">
          <h4>Add Event</h4>
        </div>
        <div className="card-body">
          <form onSubmit={this.onSubmit}>
            <TextInputGroup
              label="Title"
              name="title"
              placeholder="Enter Title..."
              value={title}
              onChange={this.onChange}
              error={errors.title}
            />
            <TextInputGroup
              label="Description"
              name="body"
              placeholder="Enter Description..."
              value={body}
              onChange={this.onChange}
              error={errors.body}
            />
            <div className="form-group">
              <label htmlFor="date">Date</label>
              <div class="row" style={{ marginTop: "10px" }}>
                <div class="col-sm-4">
                  <DatePicker
                    className="form-control"
                    selected={date}
                    format="dd-MM-YYYY"
                    placeholderText="Select Date"
                    showPopperArrow={false}
                    onChange={this.Changedate}
                    error={errors.date}
                  />
                </div>
              </div>
            </div>
            <div className="form-group">
              <label htmlFor="mode">Mode</label>
              <input
                name="mode"
                className={"form-control form-control-lg"}
                defaultValue="Online"
                value="Online"
              />
            </div>
            <TextInputGroup
              label="Rate"
              name="ticket_price"
              placeholder="Enter Rate..."
              value={ticket_price}
              onChange={this.onChange}
              error={errors.ticket_price}
            />

            <input
              type="submit"
              value="Add Event"
              className="btn btn-light btn-block"
            />
          </form>
        </div>
      </div>
    );
  }
}
AddEvent.propTypes = {
  addEvent: PropTypes.func.isRequired,
};

export default connect(null, { addEvent })(AddEvent);



